@import 'reset';
@import 'mixins';

*, *:before, *:after {
  @include box-sizing(border-box);
}

body {
  background-color: #FFE7E9;

  a {
    color: #fff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

button {
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 24px;
  border: 0;
  border-radius: 2px;
  background-color: #6ac600;
  @include box-shadow(4px 4px 0 0 rgba(42, 14, 14, 0.8));
  transition: all 0.2s ease-in-out;

  &:hover, .active {
    @include box-shadow(6px 6px 0 0px rgba(42, 14, 14, 0.9));
    transform: translate(-2px,-2px);
  }

  &:focus {
    outline: none;
  }
}

/* MEDIA QUERIES */

@media (max-width: 769px) {
  .game {
    width: 100vw;
    max-width: 100vw;
  }
}

@media screen and (max-width: 860px) {
  .brand {
    h1 {
      font-size: 1.5em;
    }
  }

  .info {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .brand {
    h1 {
      font-size: 1em;
    }
  }
}

@media screen and (max-width: 580px) {
  button, .timer {
    font-size: 16px;
    padding: 2px;
  }

  .reset-button {
    font-size: 16px;
  }
}

@media screen and (max-width: 384px) {
  button, .timer {

    font-size: 10px;
    padding: 2px;
  }

  .reset-button {
    font-size: 10px;
  }
}


/* Header */
header {
  display: block;
  width: 100%;
  height: 50px;
  background-color: #7ebfbd;
  @include shadow(1);
  margin: auto;
  nav {
    margin: auto;
    display: table;
    width: 80vw;
    height: 50px;
    color: #fff;
    font-size: 1.1em;
    
    h1 {
      color: #16282e;
      font-size: 2em;
      font-weight: bold;
      -webkit-filter: drop-shadow( -2px 3px 3px rgba(221, 235, 239, 1) );
            filter: drop-shadow( -2px 3px 3px rgba(221, 235, 239, 1) );
    }

    div {
      display: table-cell;
      vertical-align: middle;
    }

    .brand {
      width: 25%;
      text-transform: uppercase;
      text-align: left;
    }

    .info {
      text-align: right;
      font-weight: bold;
      font-style: italic;
    }

    .menu {
      @media screen and (max-width: 580px) {
        width: 50%;
      }

      text-align: right;
      width: 32%;
      .timer {
        @media screen and (max-width: 580px) {
          width: 57px;
          margin-right: 2px;
        }

        @media screen and (max-width: 384px) {
          width: 40px;
          margin-right: 0px;
        }

        width: 80px;
        margin-right: 7px;
        color: #fff;
        background-color: #16282e;
        &:hover {
          @include box-shadow(4px 4px 0 0 rgba(42, 14, 14, 0.8));
          transform: none;
        }
      }

      li, .dropdown-menu {
        position: relative;
        display: inline-block;
      }

      .dropdown-toggle {
        @media screen and (max-width: 580px) {
          background-size: 18px 18px;
          margin-left: 2px;
        }

        @media screen and (max-width: 384px) {
          background-size: 14px 14px;
        }

        margin-left: 5px;
        width: 30px;

        background: rgba(22, 40, 46, 0.7);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 24px 24px;
        background-image: url(data:image/svg+xml;utf8;base64,);
        opacity: 0.7;
      }

      .dropdown {
        z-index: 9999;
        position: absolute;
        top: 150%;
        right: -4px;
        background: rgba(22, 40, 46, 0.7);
        padding: 3px 10px;
        border-radius: 3px;
        @include shadow(3);

        button {
          display: block;
          box-shadow: none;
          text-transform: none;
          font-size: 18px;
          margin: 6px auto;
          opacity: 1;
          background: none;
          &:hover {
            transform: none;
          }
        }

        .selected, button:hover {
          background-color: #7ebfbd; 
        }
      }

    }
  }
}

/* FOOTER */
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  overflow: hidden;
  text-align: center;
  background-color: #7ebfbd;
  
  font-style: italic;
  @include shadow(2);
  p {
    display: inline-block;
    color: #fff;
    padding-top: 0;
    line-height: 50px;
    margin: auto auto;
    span {
      vertical-align: middle;
    }
    .github {
      display: inline-block;
      height: 50px;
      width: 50px;
      background-image: url('../assets/github-lt-32.png');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 32px 32px;
      &:hover{
        background-image: url('../assets/github-dk-32.png');
      }
    }
  }
}

/* Minesweep */



.game {
  display: block;
  // width: 100vh;
  // max-width: 100vh;

  width: 100vw;
  height: 100vw;
  max-width: 100vh;
  max-height: 100vh;

  padding-bottom: 30px;
  margin: 15px auto 0 auto;
  .row {
    // float: left;
    display: block;
    width: 100%;
    padding: 0 0;
    
    .tile {
      @media screen and (max-width: 715px) {
        margin: 3px;
      }

      @media screen and (max-width: 580px) {
        margin: 2px;
      }

      // float: left;
      cursor: pointer;
      position: relative;
      margin: 4px;
      height: 0;
      width: 3.58%;
      padding-bottom: 4%;
      display: inline-block;
      border-radius: 2px;
      background-color: #ff5056;

      @include box-shadow(4px 4px 0 0 rgba(42, 14, 14, 0.8));
      transition: all 0.2s ease-in-out;

      &:hover {
        @include box-shadow(6px 6px 0 0px rgba(42, 14, 14, 0.9));
        transform: translate(-2px,-2px);
      }
    }

    .opened {
      background-color: #FFC5C8;
      transform: translate(2px,2px);
      @include box-shadow(3px 3px 0 0 rgba(42, 14, 14, 0.8));
      &:hover {
        @include box-shadow(3px 3px 0 0 rgba(42, 14, 14, 0.8));
        transform: translate(2px,2px);
      }
    }

    .flagged {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }

    .mined {
      background-color: #FF8F93;
      background-repeat: no-repeat;
      background-position: center center;
      background-image: url(data:image/svg+xml;utf8;base64,);
    }

    .one, .two, .three, .four, .five, .six, .seven, .eight, .flagged, .mined {
      @media screen and (max-width: 580px) {
        background-size: 18px 18px;
      }

      @media screen and (max-width: 420px) {
        background-size: 16px 16px;
      }

      @media screen and (max-width: 360px) {
        background-size: 12px 12px;
      }

      background-repeat: no-repeat;
      background-position: center center;
      background-size: 24px 24px;
    }

    .one {
      background-image: url('../assets/one.png');
    }

    .two {
      background-image: url('../assets/two.png');
    }

    .three {
      background-image: url('../assets/three.png');
    }

    .four {
      background-image: url('../assets/four.png');
    }

    .five {
      background-image: url('../assets/five.png');
    }

    .six {
      background-image: url('../assets/six.png');
    }

    .seven {
      background-image: url('../assets/seven.png');
    }

    .eight {
      background-image: url('../assets/eight.png');
    }

  }

  &:after {
    @include clearfix;
  }
}

/***************
Game Over
****************/

.game-over {
  //display: none;
  position: fixed;
  top: 25%;
  left: 50%;
  width: 80vh;
  height: 130px;
  margin-left: -40vh;
  padding: 18px;
  text-align: center;

  font-size: 25px;
  font-weight: bold;
  color: rgba(33,33,33,0.85);
  text-shadow: 0 1px rgba(117,117,117, .4);

  background: rgba(236, 246, 245,0.9);
  @include shadow(5);
  border-radius: 0 0 8px 8px;
  border-top: 15px solid rgba(42, 14, 14, 0.8);
  span {
    font-size: 30px;
    margin-bottom: 12px;
    display: block;
  }
}




